@keyframes pulse {
  from {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  to {
    transform: scale(1);
  }
}

@keyframes bounceInLeft {
  from {
    transform: translateX(-200%);
  }

  50% {
    transform: translateX(10%);
  }

  to {
    transform: translateX(0%);
  }
}

@keyframes bounceInRight {
  from {
    transform: translateX(200%);
  }

  50% {
    transform: translateX(-10%);
  }

  to {
    transform: translateX(0%);
  }
}

@keyframes fade-In {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes menu-open {
  from {
    transform: translateY(-200%);
  }

  to {
    transform: translateY(0%);
  }
}

